        // var sheets = store.state.dbsql.sql
        // var obj = XLSX.utils.sheet_to_json(sheets['PeopleDeck'])
        // console.log(XLSX.utils.sheet_to_json(this.$store.state.dbsql.sql['Type_A']));
    },
