                num = 0,
                list = [];
            for (let i = 1; i <= 30; i++) { //å¾ªç¯30ä¸ªäºä»¶æ ä½ï¼åå§åæ ä½
                await random_monster(i)
            }

            function random_monster(i){
                return new Promise((resolve,reject)=>{
                    if(i <= 10){
                        //é¡¶ä¸10ä¸ªæ ä½çéæºäºä»¶
                        if(Math.round(Math.random()) == 1){
                            list.push(that.monsterMain[num])
                            num++
                        }else{
                            list.push(0)
                        }
                    }else if(i > 10 && i < 21){ //å¯¹ä¸­é´10ä¸ªæ ä½è¿è¡æ¡ä»¶å¤æ­
                        if(list.indexOf(i-10) == -1){
                            //å¦ææ£æµå°ä¸å¶å¯¹åºçä¸æ¹æ ä½æ²¡ææ¾ç½®äºä»¶ï¼åè¯¥æ ä½å¿ç¶äº§çä¸ä¸ªäºä»¶
                            list.push(that.monsterMain[num])
                            num++
                        }else{
                            //å¦ææ£æµå°ä¸å¶å¯¹åºçä¸æ¹æ ä½å·²ç»æ¾ç½®äºä»¶ï¼åè¯¥æ ä½éæºæ¾ç½®äºä»¶
                            if(Math.round(Math.random()) == 1){
                                list.push(that.monsterMain[num])
                                num++
                            }else{
                                list.push(0)
                            }
                        }
                    }else if(i > 20){ //å¯¹åºä¸10ä¸ªæ ä½è¿è¡æ¡ä»¶å¤æ­
                        if(list.indexOf(i-10) == -1){
                            //å¦ææ£æµå°ä¸å¶å¯¹åºçä¸æ¹æ ä½æ²¡ææ¾ç½®äºä»¶ï¼åè¯¥æ ä½å¿ç¶äº§çä¸ä¸ªäºä»¶
                            list.push(that.monsterMain[num])
                            num++
                        }else if(list.indexOf(i-10) !== -1 && list.indexOf(i-20) !== -1){
                            //å¦ææ£æµå°ä¸å¶å¯¹åºçä¸æ¹æææ ä½å·²ç»æ¾ç½®äºä»¶ï¼åè¯¥æ ä½éæºæ¾ç½®äºä»¶
                            if(Math.round(Math.random()) == 1){
                                list.push(that.monsterMain[num])
                                num++
                            }else{
                                list.push(0)
                            }
                        }else if(list.indexOf(i-10) !== -1 && list.indexOf(i-20) == -1){
                            list.push(that.monsterMain[num])
                            num++
                        }
                    }
                    resolve()
                })
            }
            that.list = _.cloneDeep(list)
        },
        // å¼å±æ¶å¶ä½å½åå¯æ¬å¯¹åºæªç©å¡æ± 
        async getMonsterData(e){
            var lva = [],
                lvb = [],
                lvc = [];
            await this.$axios.get('/GemEnum.xlsx',{responseType:'arraybuffer'}).then((res)=>{
                const XLSX = require('xlsx')
                var data = new Uint8Array(res.data),
                    wb = XLSX.read(data, {type:"array"}),
                    sheets = wb.Sheets,
                    monsterMain = XLSX.utils.sheet_to_json(sheets.Type_A), // æªç©ä¸»ä½
                    monsterSkill = XLSX.utils.sheet_to_json(sheets.Type_A_Skill), // æªç©æè½
                    skill = XLSX.utils.sheet_to_json(sheets.Skill_Type); // æè½ææ

                monsterMain.forEach((item,index)=>{
                    item.skill = _.compact(item.skill_id.split(",")).map((e)=>{
                        return _.find(monsterSkill,(o)=>{
                            return o.id == Number(e)
                        })
                    }).map((e)=>{
                        return e.skillEffect = _.compact(e.skill_type.split(",")).map((e)=>{
                            return _.find(skill,(o)=>{
                                return o.id == Number(e)
                            })
                        })
                    })
                    switch (item.life_level) {
                        case "æ®éæªç©":
                            lva.push(item)
                            break;
                        case "ç²¾è±æªç©":
                            lvb.push(item)
                            break;
                        case "é¢ä¸»æªç©":
                            lvc.push(item)
                            break;
                        default:
                            break;
                    }
                })
            })
            this.monsterMain = _.shuffle([..._.sampleSize(lva,10),..._.sampleSize(lvb,10),..._.sampleSize(lvc,10)])

            this.init()
        },
        showDeck(e,item){
            if(this.cardshow == false){
                this.cardshow = true
                TweenMax.gsap.to(`#back${item}`,0.5,{backgroundPosition:'-200% 50%,50% 50%',onComplete:()=>{this.showBattle = true}})
            }
        }
