            <div v-for="(item,index) in player_statuslist" :key="index" class="playerstatusitemtyle" :style="`background-image:url(${item.image})`" :id="`player_status${index}`" @mouseenter="statusmouseenterevent($event,item)" @mouseleave="statusmouseleaveevent($event,item)"></div>
