                    <path v-if="titleShow" id="pt2" :d="`M${svg.width*0.06} ${svg.height*0.25} L${svg.width*0.94} ${svg.height*0.25}`" fill="none"/>
                </def>
                <foreignObject x="0" y="0" width="100%" height="100%" class="fo1style">
                    <p class="cardeffectstyle" v-html="item.FTextDescription"/>
                </foreignObject>
                <image x="0" y="0" width="100%" height="20%" :xlink:href="item.FTopDecoration"></image>
                <line x1="5%" y1="23%" x2="5%" y2="90%" :stroke="item.FColor" stroke-width="1" />
                <text>
                    <textPath v-if="titleShow" class="playing_order" text-anchor="middle" startOffset="50%" fill="black" xlink:href="#pt2">{{item.FTitle}}</textPath>
                </text>
            </svg> -->
        </div>
