<script>
import '@/assets/font/font.css'
import * as TweenMax from 'gsap/all'
import battle from '@/api/battle.js'
import _ from 'lodash'
import components from "@/api/components.js"
import common from "@/api/common.js"

export default {
    data(){
        return{
            //æªç©ä¿¡æ¯
            monsterinfo:{
                monster_name:"ç¾½èç¥",//æªç©å
                hp:1000000,//è¡é
                current_hp:1000000,//å½åè¡é
            },
            msglist:[],//ææè®°å½
            message:"",
            player_statuslist:[ //ç©å®¶ç¶æ
                {titlecolor:"#C60000",name:"è¶æ°æçç¸",image:require('@/assets/playerstatus/status1.png')},
                {titlecolor:"#6600C6",name:"çå¥æç³»",image:require('@/assets/playerstatus/status2.png')},
                {titlecolor:"#3EA300",name:"ä¸ç©å¤©ç",image:require('@/assets/playerstatus/status3.png')},
            ],
            monster_statuslist:[ //æªç©ç¶æ
                {titlecolor:"#C60000",name:"è¶æ°æçç¸",image:require('@/assets/playerstatus/status1.png')},
                {titlecolor:"#6600C6",name:"çå¥æç³»",image:require('@/assets/playerstatus/status2.png')},
                {titlecolor:"#3EA300",name:"ä¸ç©å¤©ç",image:require('@/assets/playerstatus/status3.png')},
            ],
            start_end:false,//å¼å§å¯¹å± or ç»æå¯¹å±
            cardid:null,//åä¸ä¸ªéä¸­ççå¡çid
        }
    },
    watch:{
        player_statuslist(nval,oval){
            nval.forEach((item,index)=>{
                this.$nextTick(()=>{
                    if(index > 0){
                        document.getElementById(`player_status${index}`).style.left = 60*index+'px'
                    }
                })
            })
        },
        msglist(nval,oval){
            this.$nextTick(()=>{
                document.getElementsByClassName('messagestyle')[0].scrollTop = document.getElementsByClassName('messagestyle')[0].scrollHeight
            })
        },
        "monsterinfo.current_hp"(nval,oval){
            var current_hp1 = document.getElementsByClassName("monster_blood_volume_1")[0]
            var current_hp2 = document.getElementsByClassName("monster_blood_volume_2")[0]
            current_hp2.style.width = `${(nval / this.monsterinfo.hp) * 100}%`
            setTimeout(() => {
                current_hp1.style.width = `${(nval / this.monsterinfo.hp) * 100}%`
            }, 200);
            this.$nextTick(()=>{
                this.msglist.push(this.message)
            })
        },
    },
    beforeMount(){
        this.init()
    },
    mounted(){
        this.player_statuslist.forEach((item,index)=>{
            this.$nextTick(()=>{
                if(index > 0){
                    document.getElementById(`player_status${index}`).style.left = 60*index+'px'
                }
            })//7,43
        })
        this.monster_statuslist.forEach((item,index)=>{
            this.$nextTick(()=>{
                if(index > 0){
                    document.getElementById(`monster_status${index}`).style.top = 60*index+'px'
                }
            })//7,43
        })
        this.$nextTick(()=>{
            TweenMax.gsap.to("#curtainstyle_1",1,{transform:"scale(1,1)"})
            TweenMax.gsap.to(["#monster_blood_volume_1","#monster_blood_volume_2","#monster_blood_volume_5","#monster_blood_volume_6"],1,{width:"100%"})
        })
        // this.init()
    },
    methods:{
        init(){
            common.create_card(0,[]).then(async (res)=>{
                if(res.length == 0){
                    this.init()
                }else{
                    await res.forEach((item,index)=>{
                        document.getElementById("cartridge_clip").appendChild(item)
                    })
                    this.updatecardmargin(res)
                    this.$nextTick(()=>{
                        // this.getcardstyle(res[0].id)
                    })
                }
            })
        },
        // åºç
        outcard(){
            if(this.$store.state.player.start_end == true){
                return false
            }
            this.$store.dispatch("Start_End",true)
            this.cardid = this.$store.state.player.cardid
            battle.judgment_start_current(this.cardid,this.monsterinfo.monster_name).then(async (obj)=>{
                this.monsterinfo.current_hp -= obj.value
                this.message = obj.msg
                await TweenMax.gsap.to(`#${this.cardid.id}`,0.2,{delay:0.6,opacity:0,onComplete:()=>{
                    var card = document.getElementById(this.cardid.id)
                    card.parentElement.removeChild(card)
                    this.cardid = null
                    this.$store.dispatch("Cardid",{})
                }})
                var list = []
                for (let i = 0; i < document.getElementById("cartridge_clip").children.length; i++) {
                    list.push(document.getElementById("cartridge_clip").children[i])
                }
                this.updatecardmargin(list)
            })
        },
        // æ½ç
        incard(){
            common.create_card(1,[]).then(async (res)=>{
                if(res.length == 0){
                    this.incard()
                }else{
                    await res.forEach((item,index)=>{
                        document.getElementById("cartridge_clip").appendChild(item)
                    })
                    var list = []
                    for (let i = 0; i < document.getElementById("cartridge_clip").children.length; i++) {
                        list.push(document.getElementById("cartridge_clip").children[i])
                    }
                    this.updatecardmargin(list)
                    // this.$nextTick(()=>{
                    //     this.getcardstyle()
                    // })
                }
            })
        },
        statusmouseenterevent(e,item){ //é¼ æ ç§»å¥ç¶æäºä»¶
            components.playerstatuinfo(document.getElementById("curtainstyle"),item)
        },
        statusmouseleaveevent(e,item){ //é¼ æ ç§»åºç¶æäºä»¶
            components.playerstatuinfo(document.getElementById("curtainstyle"),item)
        },
        //çç»é´è·æ´æ°
        updatecardmargin(nval){
            if(nval.length <= 7){
                nval.forEach((item,index)=>{
                    this.$nextTick(()=>{
                        document.getElementById(item.id).style.left = 140*index+'px'
                        // console.log(TweenMax.gsap.getTweensOf(`#card${index}`));
                        // TweenMax.gsap.to(`#card${index}`,0,{opacity:1,top:'10px'})
                    })//7,43
                })
            }else{
                var divwidth = document.getElementsByClassName('curtainstyle_2')[0].offsetWidth
                var superimposed_value = nval.length == 8 ? 0 : ((120 * nval.length) - divwidth) / (nval.length - 1)
                nval.forEach((item,index)=>{
                    this.$nextTick(()=>{
                        // if(index > 0){
                            document.getElementById(item.id).style.left = (120-superimposed_value)*index+'px'
                        // }
                    })
                })
            }
            this.$store.dispatch("Start_End",false)
        },
        //è·åsvgå¡çstyle
        // getcardstyle(id){
        //     this.svg.width = document.getElementById(id).getBBox().width;
        //     this.svg.height = document.getElementById(id).getBBox().height;
        //     this.$nextTick(()=>{
        //         this.titleShow = true
        //     })
        // }
    }
}
</script>

